.Card_Card {
	display: flex;
	width: 80%;
	margin: 50px auto;
	border: .1px solid #ccc;
	box-shadow: 2px 3px 10px #eee;
	cursor: pointer;
}

.Card_description {
	width: 100%;
	padding: 10px;
	/* padding: 10px; */
}

div .Card_img {
	margin: 0;
}

.Card_Img {
	object-fit: fill;
	height: 100%;
}

@media (max-width: 774px) {
	.Card_Card img {
		width: 100%;
		height: 100%;
	}
	.Card_Card {
		display: block;
	}
}
